import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { AiFillWechat } from "react-icons/ai";
import { useNavigate } from "react-router-dom";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import "../../../assets/css/ahmad.css";
import { getUserList } from "../../../app/features/userManagement/user.slice";
import { useDispatch, useSelector } from "react-redux";
import { PATH } from "../../../utils/path";

export default function Staff() {
  const [searchValue, setSearchValue] = useState();
  const [roleValue, setRoleValue] = useState(null);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { getAllUser } = useSelector((state) => state.userRole);

  function staffChatFunct(data) {
    navigate(PATH.STAFF_CHAT.replace(":staffId", data?.userId), {
      state: {
        staffData: data,
      },
    });
  }

  const columns = [
    {
      dataField: "userId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },

    {
      dataField: "patientName",
      text: "Staff Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text, row) => (
        <div className="d-flex align-items-center">
          <img
            src={`https://ui-avatars.com/api/?name=${`${row?.firstName} ${row?.lastName}`}&background=000071&color=fff`}
            alt="patient"
            className="me-2"
            style={{ borderRadius: "50%", width: "3.3rem" }}
          />
          <span style={{ lineHeight: "1.2" }}>
            <p className="m-0 table-bold-text">{`${row?.firstName} ${row?.lastName}`}</p>
            <p className="m-0 table-normal-text">{row?.email}</p>
            <p className="m-0 table-normal-text" style={{ color: "#999999" }}>
              {row?.phoneNumber}
            </p>
          </span>
        </div>
      ),
    },
    {
      dataField: "staffRole",
      text: "Role",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "genderName",
      text: "Gender",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },

    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (text, row) => (
        <Button
          onClick={() => staffChatFunct(row)}
          className="text-decoration-none table-action"
          style={{ background: "transparent", borderColor: "transparent" }}
        >
          <AiFillWechat /> Chat
        </Button>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      {from} to {to} out of {size} entries
    </span>
  );

  const paginateOptions = {
    paginationSize: 10,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };

  useEffect(() => {
    const finalData = {
      roleId: 5,
      search: searchValue,
      staffRoleId: roleValue ? +roleValue : null,
    };
    dispatch(getUserList({ finalData }));
  }, [dispatch, searchValue, roleValue]);

  return (
    <div className="staff_main">
      <h5>Staff</h5>
      <Card>
        <Card.Body className="p-0">
          <Row className=" px-4 pt-3">
            <Col md={3}>
              <span className="d-flex align-self-center">
                <Form.Control
                  onKeyDown={(e) => {
                    if (e.key === "Enter") {
                      setSearchValue(e.target.value);
                    }
                  }}
                  onChange={(e) => {
                    if (e.key === "Enter") {
                      setSearchValue(e.target.value);
                    }
                  }}
                  type="text"
                  placeholder="Search"
                  className="me-2 mb-3 search-field-spacing"
                  aria-label="Search"
                />
                <BiSearch size={22} className="searchbar-icon" />
              </span>
            </Col>
            <Col
              md={9}
              className="d-flex flex-wrap align-self-center justify-content-end"
            >
              <div className="d-flex mb-2 mr-2">
                <select
                  onChange={(e) => setRoleValue(e.target.value)}
                  className="form-select pe-5"
                  aria-label="Default select example"
                >
                  <option selected>Role</option>
                  <option value={6}>Nurse</option>
                  <option value={7}>Receptionist</option>
                </select>
              </div>
            </Col>
          </Row>

          <span className="doctor-datatable">
            <BootstrapTable
              columns={columns}
              data={getAllUser?.data ? getAllUser?.data : []}
              keyField="id"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              bordered={false}
              wrapperClasses="table-responsive"
              className="selection-cell-header "
            />
          </span>
        </Card.Body>
      </Card>
    </div>
  );
}
